import { format, parseJSON } from "date-fns"
import { useUser } from "@clerk/clerk-react"
import { UserAvatar } from "@rp/react/web/common/base-components"
import { Box, Skeleton, Typography } from "@rp/react/web/mui/core"

type BriefDashSubheaderProps = {
  reportDate: string | undefined
}
const BriefReportSubheader = ({ reportDate }: BriefDashSubheaderProps) => {
  const { user } = useUser()

  return (
    <Box
      sx={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        flexDirection: "row",
        marginTop: "16px",
      }}
      role="article"
      aria-label="brief report subheader"
    >
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          color: "text.secondary",
        }}
      >
        {user ? (
          <>
            <UserAvatar ownerName={user?.fullName ?? ""} size={24} />

            <Typography sx={{ fontSize: "12px", px: "8px" }}>You</Typography>
          </>
        ) : (
          <Skeleton height={32} width={50} style={{ marginTop: "0", padding: 0 }} />
        )}

        <Typography sx={{ fontSize: "12px" }}>·</Typography>

        <Typography sx={{ fontSize: "12px", px: "8px" }}>
          {reportDate ? format(parseJSON(reportDate), "HH:mm") : " -- "}
        </Typography>

        <Typography sx={{ fontSize: "12px" }}>·</Typography>

        <Typography sx={{ fontSize: "12px", px: "8px" }}>
          {reportDate ? format(parseJSON(reportDate), "MMMM d, yyyy") : " -- "}
        </Typography>
      </Box>
    </Box>
  )
}

export { BriefReportSubheader }
